import type { RouteTable } from "../shared/types.js";
import { routes as table } from "../shared/routes.js";

// Utilitaires front légers (aucun import Node)
export function getRoutes(): RouteTable {
  return table;
}

export function findRoute(name: string) {
  return table.find((r) => r.name === name);
}

/** Génère un path en remplaçant les params :id */
export function url(name: string, params: Record<string, string | number> = {}) {
  const r = findRoute(name);
  if (!r) throw new Error(`Route '${name}' not found`);
  return r.path.replace(/:([A-Za-z_]+)/g, (_, k: string) => encodeURIComponent(String(params[k] ?? `:${k}`)));
}